import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Icon, Label, Table, Button, Input, Menu, Dropdown, Segment} from 'semantic-ui-react';
import TableTorrents from './TableTorrents';
import NavigationMenu from './NavigationMenu';
import TabsMenu from './TabsMenu';
import SegmentData from './SegmentData';
import SideMenu from './SideMenu';
import {Grid} from 'semantic-ui-react';

class App extends Component {

	render() {
		return (
			<Grid className="App" columns={16}> 
					<Grid.Column width={16}>
						<NavigationMenu />
					</Grid.Column>
						
			
						<Grid.Column width={3}>
						<SideMenu />
						</Grid.Column>
						
						<Grid.Column width={13} >	
					
						<Grid.Column>
						<TableTorrents />
						</Grid.Column>

						<Grid.Column>
						<TabsMenu />
						</Grid.Column>
						
						</Grid.Column>
					
			
					<Grid.Column width={16}>
						<SegmentData />
					</Grid.Column>
			
			</Grid>

		);
	}

}


			{/* <div className="App">
			<NavigationMenu />	
			<TableTorrents />
			<TabsMenu />
			<SideMenu />
			<SegmentData />
			</div> */}

export default App;



