import React, { Component } from 'react'
import { Dropdown, Icon, Input, Menu, Checkbox} from 'semantic-ui-react'

class SideMenu extends Component {
  state = {}

  handleItemClick = (e, { name }) => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state

    return (
      <Menu vertical>
        <Menu.Item>
          Torrents
          <Menu.Menu>
            <Menu.Item name='downloading' active={activeItem === 'downloading'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Downloading</label>} />
            </Menu.Item>
            <Menu.Item name='seeding' active={activeItem === 'seeding'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Seeding</label>} />
            </Menu.Item>
            <Menu.Item name='completed' active={activeItem === 'completed'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Completed</label>} />
            </Menu.Item>
            <Menu.Item name='paused' active={activeItem === 'paused'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Paused</label>} />
            </Menu.Item>
            <Menu.Item name='active' active={activeItem === 'active'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Active</label>} />
            </Menu.Item>
            <Menu.Item name='inactive' active={activeItem === 'inactive'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Inactive</label>} />
            </Menu.Item>
            <Menu.Item name='error' active={activeItem === 'error'} onClick={this.handleItemClick}>
              <Checkbox label={<label>Error</label>} />
            </Menu.Item>
	    </Menu.Menu>
        </Menu.Item>

        <Menu.Item name='tags' active={activeItem === 'tags'} onClick={this.handleItemClick}>
          Tags
          <Menu.Menu>
		    <Menu.Item name='movies' active={activeItem === 'movies'} onClick={this.handleItemClick}>
		      <Checkbox label={<label>Movies</label>} />
		    </Menu.Item>
		    <Menu.Item name='books' active={activeItem === 'books'} onClick={this.handleItemClick}>
		      <Checkbox label={<label>Books</label>} />
		    </Menu.Item>
	    </Menu.Menu>
	    
	</Menu.Item>
        <Menu.Item name='trackers' active={activeItem === 'trackers'} onClick={this.handleItemClick}>
          Trackers
          <Menu.Menu>
		    <Menu.Item name='the-pirate-bay' active={activeItem === 'the-pirate-bay'} onClick={this.handleItemClick}>
		      <Checkbox label={<label>The Pirate Bay</label>} />
		    </Menu.Item>
		    <Menu.Item name='anime-no-sekai' active={activeItem === 'anime-no-sekai'} onClick={this.handleItemClick}>
		      <Checkbox label={<label>Anime No Sekai</label>} />
		    </Menu.Item>
	    </Menu.Menu>
	    
	 </Menu.Item>
	 
	    {/*  <Dropdown item text='More'>
          <Dropdown.Menu>
            <Dropdown.Item icon='edit' text='Edit Profile' />
            <Dropdown.Item icon='globe' text='Choose Language' />
            <Dropdown.Item icon='settings' text='Account Settings' />
          </Dropdown.Menu>
        </Dropdown>   */}
      </Menu>
    )
  }
}

export default SideMenu;



