import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Icon, Label, Table, Button, Input, Menu, Dropdown, Segment} from 'semantic-ui-react'


const tableData = [
  { name: 'Jimmy', status: 'Requires Action', notes: 'Nice' },
  { name: 'Jamie', status: 'Online', notes: 'Hostile' },
];

const headerRow = [
  'Name',
  'Status',
  'Notes',
]

const renderBodyRow = ({ name, status, notes }, i) => ({
  key: name || `row-${i}`,
  cells: [
    name,
    status,
    notes,
  ],
})


class TableTorrents extends Component {
	
	componentDidMount() {
		setInterval(
			function(){ fetch('http://localhost:8040/session/torrents')
			.then(response => {
			  if (response.ok) {
			    return response.json();
			  } else {
			    throw new Error('Something went wrong ...');
			  }
			})
			.then(data => this.setState({ data, isLoading: false }))
			.catch(error => this.setState({ error, isLoading: false }));
			}.bind(this), 2000);


	}

	render() {
		return (
			<div className="TableTorrents">
			
			  <Table
			    celled
			    headerRow={headerRow}
			    renderBodyRow={renderBodyRow}
			    tableData={tableData}
			  />

			</div>
		);
	}

}

export default TableTorrents;



